import React, {Component, useState} from 'react';

import './resources/css/App.css';
import HomePage from './pages/HomePage';
import TopicListPage from './pages/TopicListPage';
import TopicDetailPage from './pages/TopicDetailPage';
import QuizListPage from './pages/QuizListPage';
import QuizPage from './pages/QuizPage';
import NotFoundPage from './pages/NotFoundPage';
import ContactPage from './pages/ContactPage';
import {BrowserRouter as Router, Route,Switch,} from 'react-router-dom';
import {loadScript} from './utils/Util';
import {PAGE} from './../src/utils/Const';

class App extends Component {
  constructor() {
    super();
    const langu = window.navigator.languages.includes('vi')? 'vi' : 'en';
    localStorage.setItem('langu', langu);
    console.log("===1" + localStorage.getItem('langu'))
    this.handlePageChange = this.handlePageChange.bind(this);
    this.state = { 
      someState: '',
    };
  }

  handlePageChange(val) {
    this.setState((state) => { return {someState: val}});
  }

  componentWillMount() {
    loadScript(process.env.PUBLIC_URL + '/libs/skulpt.min.js')
    loadScript(process.env.PUBLIC_URL + '/libs/skulpt-stdlib.js');    
  }


  render() {
    return (
      <Router>
        <div className="App">
            <Switch>
              <Route path={PAGE.home.path} component={HomePage} exact />
              <Route path={PAGE.topic_list.path} component={TopicListPage} />
              <Route path={PAGE.topic.path + "/:id"} component={TopicDetailPage} />
              <Route path={PAGE.quiz_list.path} component={QuizListPage} />
              <Route path={PAGE.quiz.path + "/:id"} component={QuizPage} />
              <Route path={PAGE.contact.path} component={ContactPage} />
              <Route component={NotFoundPage} />
            </Switch>
          </div>
      </Router>
    );
  }
}

export default App; 