import marked from 'marked';
import PageText_en from './../resources/content/en/PageText';
import i18n_en from './../resources/content/en/i18n';
import PageText_vi from './../resources/content/vi/PageText';
import i18n_vi from './../resources/content/vi/i18n';


const textProvider = (key) => {
    const langu = (localStorage.getItem('langu') == null) ? 'en': localStorage.getItem('langu')
    return langu == 'en'?  PageText_en[key] :  PageText_vi[key];
}

const i18n = (key) => {
    const langu = (localStorage.getItem('langu') == null) ? 'en': localStorage.getItem('langu')
    const res = langu === 'en'?  i18n_en[key] :  i18n_vi[key];
    return res;
}


const mdProcessor = (content) => {
    var id = -1;
    
    const formatSampleCode = (str) => {
        return str.replace(/<s_c>(.*?)<\/s_c>/sg, (tagstr) => {
            const codeStr = tagstr.replace(/<\/?s_c>/sg,'').replace(/^\s+|\s+$/g, '');;
            return formatCode(codeStr);
            }) 
    }

    const contentWithPracticeCode = content.split("</p_c>").map(e => {
        
        const ptag = e.match(/<p_c>|<p_c size=.>/) === null ? null : e.match(/<p_c>|<p_c size=.>/)[0];
        const size = (ptag === null || ptag.match(/size=./) === null) ? "S" : ptag.match(/size=./)[0].replace("size=", '');        
        const[t,c] = e.split(/<p_c>|<p_c size=.>/);
        const [pc, ac] = (c === undefined) ? [undefined,undefined] : c.split("<p_a_c>");
        const ac_trim = (ac === undefined) ? undefined : ac.split("</p_a_c>")[0];    
        const ac_format = ac_trim === undefined ? undefined : formatSampleCode(ac_trim);

        const pcode = (pc === undefined ) ? undefined : pc.replace(/^\s+|\s+$/g, '');
        const hasTurtle = (pcode === undefined)? false : pcode.includes(' turtle');
        id++;
        return {id: id, content: t, practiceCode: pcode, hint: ac_format, size: size};
    })

    const res = contentWithPracticeCode.map(o => {
        const fmstr = formatSampleCode(o.content);
        return {id: o.id, content: textConvertMD_S(fmstr) , practiceCode: o.practiceCode, hint: o.hint, size: o.size};
    })
    
    return res;
}


const textConvertMD_S = (md) => {
    return marked(md);
}


const formatCode = (code) => {

    if (window.ace == undefined) return code;
    const ace = window.ace;
    const highlight = require("ace-builds/src-noconflict/ext-static_highlight");
    const Mode = ace.require("ace/mode/python").Mode;
    const theme = ace.require("ace/theme/cobalt");

    var highlighted = highlight.render(code, new Mode(), theme);        
    var str = '<html><body>\n' +
                '<style type="text/css" media="screen">\n' +
                highlighted.css +
                '</style>\n' + 
                highlighted.html +
            '</body></html>';
    // console.log("===INLINE CODE === " + str);        
    return str;        

}

const getH2Elements = (mdtext) => {
    const a = mdtext.match(/^## .*$/gm).map(e => {
        const val = e.replace(/## +/, '');
        const key = e.replace(/## +/, '').replace(/[,:;?()]/g, ' ').toLowerCase().trim().split(/\s+/g).join("-").replace(/[&]/g, '');
        return [key,val]
    }) ;
    return a;
}

export {textProvider, i18n, formatCode, mdProcessor, getH2Elements}