import React, {useState,useEffect} from 'react';
import './../resources/css/CodeBox.css';
import AceEditor from "react-ace"; 
import "ace-builds/src-noconflict/mode-python";
import "ace-builds/src-noconflict/theme-cobalt";
import ShowHideBox from './../components/ShowHideBox';
import {i18n} from './../utils/TextUtil';


const CodeBox = ({initCode, id = 0, size = "S"}) => {
    const defaultCode = initCode === undefined ? '' : initCode;     
    const [inputCode, setInputCode] = useState(defaultCode);

    const handleInputCodeChange = val => {
        setInputCode(val);
    }

    const handleClean = () => {
        var ct = document.getElementById("result-box-" + id);
        ct.innerHTML = '';
    }

    const intp_callback = (id, val, error) => {
        console.log("-- id " + id + " val: " + val + " err: " + error);
            var ct = document.getElementById("result-box-" + id);
            error ? ct.innerHTML = ct.innerHTML + '<span class="text-error">' + val + '</span>' :
            ct.innerHTML = ct.innerHTML + val ;
    }

    const showInput = (prompt) => {

        return new Promise((resolve, reject) => {
            var ct = document.getElementById("result-box-" + id);
            ct.innerHTML = ct.innerHTML
            setTimeout(function() {
                const value = window.prompt(prompt);
                resolve(value);
              },0);    
        });
    }

    const handleRun = () => {

        const Sk = window.Sk;    
        

        const outf = res => {
            intp_callback(id, res, false);    
        }  
        const builtinRead = x => {
            if (Sk.builtinFiles === undefined || Sk.builtinFiles["files"][x] === undefined)
                    throw "File not found: '" + x + "'";
            return window.Sk.builtinFiles["files"][x];
        }

        Sk.pre='result-box-' + id;
        Sk.configure({
            // inputfun: function (prompt) {
            //     return window.prompt(prompt);
            // },
            inputfun: showInput,
            inputfunTakesPrompt: true,
            output: outf,
            __future__: Sk.python3,
            read: builtinRead
        });

        (Sk.TurtleGraphics || (Sk.TurtleGraphics = {})).target = 'result-box-' + id;
        var myPromise = Sk.misceval.asyncToPromise(function() {
            return window.Sk.importMainWithBody("<stdin>", false, inputCode, true);
        });
        myPromise.then(function(mod) {
            console.log('success');
        },
            function(err) {
                intp_callback(id, err, true);
        });
            

    };

    return(
        <>
        <div className= {"code-box-" + size}>
            <div  className={"grid-container-code-box-" + size}  >
                <div className="grid-code-editor">
                    <div className="card">
                        <AceEditor mode="python" theme="cobalt" onChange={handleInputCodeChange} value={inputCode}
                            height="100%" width="100%"/>
                    </div>
                </div>
                <div className="grid-action-bar">    
                    <div className="card">
                        <div className="action-bar-nav">
                            <ul >
                                <li><a onClick={handleRun}>{i18n('link_run')}</a></li>
                                <li><a onClick={handleClean}>{i18n('link_clean')}</a></li>
                            </ul>    
                        </div>
                    </div>
                </div>
                <div className="grid-result-box" id="runResultBox">
                    <div className="card">
                        <pre className="result-box" id={"result-box-" + id}>
                        </pre>
                    </div>
                </div>
            </div>  
            
        </div>   
        </>
        

    )
};


export default CodeBox;