import React, {useState, useEffect} from 'react';
import {PAGE} from './../utils/Const';
import Header from './../components/Header';
import './../resources/css/CodeBox.css';
import TextContentBox from './../components/TextContentBox';
import { useParams } from "react-router-dom";
import {pageContentLoader, pageMetadataLoader} from './../utils/Util';
import BottomNav from './../components/BottomNav';
import {getH2Elements} from './../utils/TextUtil';
import './../resources/css/SideNav.css';



const TopicDetailPage = () => {

    const {id} = useParams();
    const [body, setBody] = useState('');
    const [toc, setToc] = useState([]);
    const [topicMD, setTopicMD] = useState({s_name:"", next:"", previous:"", action:""});


    useEffect(() => {
        const fetchData = async () => {
            const content = await pageContentLoader(PAGE.topic.id, id);  
            setBody(content.body);
            setToc(getH2Elements(content.body));
            const tmd = await pageMetadataLoader(PAGE.topic, id);
            setTopicMD(tmd);
        }
        fetchData();


    },[id]);

    useEffect(() => {
        
        const isChrome = /Chrome/.test(navigator.userAgent) && /Google Inc/.test(navigator.vendor);
        if (window.location.hash && isChrome) {            
                var hash = window.location.hash;
                window.location.hash = "";
                window.location.hash = hash;
                console.log("chrome " + window.location.hash)
        };

        

    },[window.location.hash])

    useEffect(() => {
        console.log("ready? " + document.readyState)
        if (document.readyState === 'complete') {
            updateSideNav(); 
        } else {
            window.addEventListener('load', updateSideNav) 
        }         
    })


    const updateSideNav = () => {

        // console.log(document.readyState + " -- TopicDetail callback --- h2 size " + document.querySelectorAll('h2[id]').length + " -- nav size " + document.querySelectorAll(`nav ol li a[href]`).length);

        if (document.querySelectorAll('h2[id]').length > 0 && document.querySelectorAll(`nav ol li a[href]`).length > 0) {
            console.log("updateSideNav");
            const observer = new IntersectionObserver(entries => {
                entries.forEach(entry => {
                    const id = entry.target.getAttribute('id');
                    const el = document.querySelector(`nav ol li a[href="#${id}"]`);
                    // console.log("nav id = " + id + "  el is null " + (el === null) + " -- intersectionRatio = " + entry.intersectionRatio);
                    if (entry.intersectionRatio > 0) {                        
                        if (el !== null) el.parentElement.classList.add('active');
                    } else {
                        if (el !== null) el.parentElement.classList.remove('active');
                    }
                });
            });
            
            // Track all sections that have an `id` applied
            document.querySelectorAll('h2[id]').forEach((section) => {
                observer.observe(section);
            });  
        }          
    }


    return (
        <> 
        <Header currentPage={PAGE.topic.id} pageLabel={topicMD.s_name} /> 
        <div id="page-body">            
        <div className="site-body">
            <div className="body-and-sidebar">
                <div id="page-body">
                    <TextContentBox content={body} id={id} onLoaded={updateSideNav}/>
                    <BottomNav next={topicMD.next} previous={topicMD.previous} action={topicMD.action} />
                </div>
                <div id="page-sidebar">
                <nav className="side-nav">
                    <ol>
                        {toc.map((e) => {
                            return (
                                <li key={e[0]}><a href={"#" + e[0]}>{e[1]}</a></li>            
                            )
                        }

                        )}


                    </ol>
                </nav> 
                </div>    
            </div>            
        </div>
        </div>
        </>
    );
    
}
export default TopicDetailPage;