import React from 'react';
import './../resources/css/BottomNav.css';
import {i18n} from './../utils/TextUtil';

const BottomNav = ({next, previous, action}) => {

    return (
        <>

        <div className="bottom-nav">
            <div className="bottom-nav-centered" style={action === undefined || action.target === undefined ? {display:"none"} : {display:"block"}}>
                {action !== undefined && action.target !== undefined && <a href={action.target} className="active">{action.name}</a>}
            </div>
            
    <a href={previous} style={previous === undefined ? {display:"none"} : {display:"block"}}>{i18n('link_previous')}</a>
            
            
            <div className="bottom-nav-right" style={next === undefined ? {display:"none"} : {display:"block"}}>
                <a href={next}>{i18n('link_next')}</a>
            </div>
            
        </div>
        </>
    )
}
export default BottomNav;