import React, { useState, useEffect } from 'react';
import './../resources/css/ShowHideBox.css';
import {i18n} from './../utils/TextUtil';

const ShowHideBox = ({content, id}) => {
    useEffect(() => {
        const el = document.getElementById("show-hide-box-" + id );
        el.innerHTML = content;
    })
    return (
        <>
        <div className="show-hide-box">
            <label className="trigger">
    <input type="checkbox" className="checkbox checkbox--red" /> {i18n('link_showanswer')}
            <span className="msg">
                <div id={"show-hide-box-" + id}></div>
            </span>
            </label>
        </div>
        </>
    )
}

export default ShowHideBox;