import React, {useEffect, useState} from "react";
import {PAGE} from './../utils/Const';
import Header from './../components/Header';
import TextContentBox from './../components/TextContentBox';
import {pageContentLoader} from './../utils/Util';
import BottomNav from './../components/BottomNav';
import {i18n} from './../utils/TextUtil';

const HomePage = () => {
    const[body, setBody] = useState('');

    useEffect(() => {
        const fetchData = async () => {
            const content = await pageContentLoader(PAGE.home.id);
            setBody(content.body);        
        }
        fetchData();
    })

    return (
        <> 
        <Header currentPage={PAGE.home.id} pageLabel={i18n('home_page_title')} /> 
        <div id="page-body">            
            <div className="site-body">
                <div className="body-and-sidebar">
                    <div id="page-body">
                        <TextContentBox content={body} />
                        <BottomNav next="/topiclist"/>
                    </div>
                    <div id="page-sidebar">
                    </div>    
                </div>            
            </div>
        </div>
        </>
    );
    
}

export default HomePage;