import React, {useEffect, useState} from "react";
import {PAGE} from './../utils/Const';
import Header from './../components/Header';
import QuizCardList from '../components/QuizCardList';
import {fileLoader} from './../utils/Util';


const QuizListPage = () => {
    const [pageContent, setPageContent] = useState([]);
    useEffect(() => {
        const fetchData = async () => {
            const content = await fileLoader(PAGE.quiz_list.id + '.json');  
            const contentWithProgress = content.map(e => {
                const cache = localStorage.getItem('quiz_' + e.id);
                const prog = (cache === null || cache === undefined)? "0%" : cache;
                e["progress"] = prog;
            })            
            setPageContent(content);
        }
        fetchData();

    },[]);


    return (
        <>
        <Header currentPage={PAGE.quiz_list.id} /> 
        <div id="page-body">            
        <div className="site-body">
            <div className="body-and-sidebar">
                <div id="page-body">
                    <QuizCardList cardList={pageContent}/>
                </div>
                <div id="page-sidebar">
                </div>    
            </div>            
        </div>
        </div>

        </>
    )
}
export default QuizListPage;