import React from 'react';
import { Link } from 'react-router-dom';
import './../resources/css/Header.css';
import './../resources/css/Nav.css';
import logo from './../resources/images/logo2.png'
import { PAGE } from '../utils/Const';
import { textProvider,i18n } from '../utils/TextUtil';

const Header = ({currentPage, pageLabel}) => {
    return (
        <>
        <div className="header-nav">
                    <ul >
                        <li><Link to={{pathname: PAGE.home.path, state: {currentPage: PAGE.home.id}}} className={currentPage === PAGE.home.id ? "navicon active" : "navicon inactive" }><input type="image" src={logo} width="48" height="48" /></Link></li>
                        <li><Link to={{pathname: PAGE.topic_list.path, state: {currentPage: PAGE.topic_list.id}}} className={currentPage.startsWith(PAGE.topic.id) ? "active" : "inactive" } >{textProvider(PAGE.topic_list.id).s_name}</Link></li>
                        <li><Link to={{pathname: PAGE.quiz_list.path, state: {currentPage: PAGE.quiz_list.id}}} className={currentPage.startsWith(PAGE.quiz.id) ? "active" : "inactive" } >{textProvider(PAGE.quiz_list.id).s_name}</Link></li>
                        <li><Link to={{pathname: PAGE.contact.path, state: {currentPage: PAGE.contact.id}}} className={currentPage === PAGE.contact.id ? "active" : "inactive" }>{textProvider(PAGE.contact.id).s_name}</Link></li>
                      </ul>    
        </div>

        <div className="site-header-top">
            <div className="content">
                {i18n('page_title')}
            </div>
        </div>
        <div className="site-header-bottom">
            <div className="text">{(pageLabel === undefined) ? textProvider(currentPage).s_name : pageLabel}</div> 
        </div>
        </>
    )
}

export default Header;