import {textProvider} from './TextUtil';
import { PAGE } from './Const';

const loadScript = url => {
    const script = document.createElement('script');
    script.src = url;
    script.async = false;
    document.body.appendChild(script);
}

const waitForLibLoaded = (lib, success) => {
    var n = 0;
    var found = window[lib] !== undefined;

    const wait = (lib) => {

        if (!found && n < 10) {
            n = n + 1;
            if( window[lib] === undefined ) {
                console.log("waiting for lib %s to load - %s", lib, n);
                setTimeout(() => {return wait(lib)},500);
            } else {
                found = true;
                success();
            }
        }
    }
    wait(lib);    
}

const intepreter = (code, callback) => {
    const w = new Worker(process.env.PUBLIC_URL + '/libs/worker.js');
    w.onmessage = function(event) {
        console.log("-- event.data " + event.data);
        let data = JSON.parse(event.data);
        if (data.type === 'exit') {
            w.terminate();
        }
        if (data.type === 'stdout') {
            callback(data.data, false);
            console.log(data.data);        
        }
        if (data.type === 'stderr') {
            callback(data.data.description, true);
            console.log('Error:', data.data);
        }
    }
    w.postMessage(code);

}

const fileLoader = async (fileName) => {
    // console.log("Loading ", process.env.PUBLIC_URL + "/data/" + fileName);
    const langu = localStorage.getItem('langu') === null || localStorage.getItem('langu') === undefined ? 'en' : localStorage.getItem('langu');
    const resp = await fetch(process.env.PUBLIC_URL + "/data/pages/" +langu+ "/" + fileName);
    const ext = fileName.substr(fileName.lastIndexOf('.') + 1);
    const res = (ext === 'json') ? await resp.json() : await resp.text();
    // console.log(" fileLoader " + res);
    return res;
}

const pageContentLoader = async (page, pageId, fileType = 'md') => {
    const fname = (pageId > 0) ? page + "_" + pageId : page;     
    const md = await fileLoader(fname + "." + fileType);
    return {
        s_name: textProvider(page).s_name,
        l_name: textProvider(page).l_name,
        body: await md,
    }
}

export {loadScript, waitForLibLoaded, intepreter, fileLoader, pageContentLoader}

export const pageMetadataLoader = async (page, currentPageId) => {
    const mdl = await fileLoader(page.id + '_list.json');
    const md = mdl.filter(p => p.id === currentPageId)[0];
    const maxId = mdl.length;
    const intId = parseInt(currentPageId);
    console.log("currentPageId " + currentPageId + " -- page " + page + " -- maxId " + maxId)
    const next = ((intId + 1) <= maxId) ? page.path + "/" + (intId+1) : undefined;
    const previous = ((intId - 1) >= 1) ? page.path + "/" + (intId-1) : undefined;
    const action = (md.action_id === null) ? undefined : {target: md.action_target, name: textProvider(md.action_id).s_name};

    md["previous"] =  previous;
    md["next"] = next;
    md["action"] = action;
    return md;
} 