import React, {useEffect,useState} from "react";
import {PAGE} from './../utils/Const';
import Header from './../components/Header';
import TopicCardList from '../components/TopicCardList';
import {fileLoader} from './../utils/Util';


const TopicListPage = () => {

    const [pageContent, setPageContent] = useState([]);
    useEffect(() => {
        const fetchData = async () => {
            const content = await fileLoader(PAGE.topic_list.id + '.json');              
            setPageContent(content);
        }
        fetchData();

    },[]);


    return (
        <> 
        <Header currentPage={PAGE.topic_list.id} /> 
        <div id="page-body">            
        <div className="site-body">
            <div className="body-and-sidebar">
                <div id="page-body">
                    <TopicCardList cardList={pageContent}/>
                </div>
                <div id="page-sidebar">
                </div>    
            </div>            
        </div>
        </div>
        </>
    );
    
}
export default TopicListPage;