import React, {useEffect, useState} from 'react';
import {mdProcessor} from './../utils/TextUtil';
import CodeBox from './CodeBox';
import ShowHideBox from './ShowHideBox';

const TextContentBox = ({content, id, onLoaded}) => {   
    useEffect(() => {
        contentList.map( e => {
            const el = document.getElementById('textContentBox' + e.id);
            el.innerHTML = e.content;
        });
    })

    if (document.readyState === 'complete') {
        if (onLoaded !== undefined) onLoaded();
    }
    const contentList = (content === undefined)? [] : mdProcessor(content);


    return (
        <>
        {contentList.map(e => {
            return (
                <div key={e.id}>
                    {(e.content !== undefined) && <div id={"textContentBox" + e.id}></div>}
                    {(e.practiceCode !== undefined) && <CodeBox initCode={e.practiceCode} size={e.size} id={e.id}/> }
                    {e.hint !== undefined && <div className="showHideBox"><ShowHideBox content ={e.hint} id={e.id}/></div>  }
                </div>    
            )
        })}
        </>
    )
}

export default TextContentBox;