import React, {useState, useEffect} from "react";
import {PAGE} from './../utils/Const';
import Header from './../components/Header';
import Quiz from "./../libs/quiz/Quiz";
import { useParams } from "react-router-dom";
import {pageContentLoader,pageMetadataLoader} from './../utils/Util';
import BottomNav from './../components/BottomNav';
import {getMDStr} from './../resources/content/QuizFormatStr';
import { logRoles } from "@testing-library/react";



const QuizPage = () => {
    const {id} = useParams();
    const [body, setBody] = useState({questions:[]});
    const [showNav, setShowNav] = useState(false);
    const [quizMD, setquizcMD] = useState({s_name:"", next:"", previous:"", action:""});

    const handleComplete = (quizRes) => {

        const {correctPoints, totalPoints} = quizRes;
        localStorage.setItem('quiz_' + id, Math.round(correctPoints * 100 / totalPoints) + "%")

        // console.log(correctPoints,totalPoints )
        setShowNav(true);
    }

    useEffect(() => {
        const fetchData = async () => {
            const content = await pageContentLoader(PAGE.quiz.id, id, "json");  
            const quiz = content.body;
            quiz.questions.map(q => {
                if (q.question.startsWith('<pqbcode>_')) {
                    q.question = getMDStr(q.question).replace(/<pcode>/g, "```");
                } 
            })
            setBody(quiz);
            const qmd = await pageMetadataLoader(PAGE.quiz, id);
            setquizcMD(qmd);

        }
        fetchData();

    },[id]);
    return (
        <> 
        <Header currentPage={PAGE.quiz.id} pageLabel={quizMD.s_name} /> 
        <div id="page-body">            
        <div className="site-body">
            <div className="body-and-sidebar">
                <div id="page-body">
                    <Quiz quiz={body} shuffle={true} showInstantFeedback={true} continueTillCorrect={true} onComplete={handleComplete} />
                    { showNav && <BottomNav next={quizMD.next} previous={quizMD.previous} action={quizMD.action} /> }
                </div>
                <div id="page-sidebar">
                </div>    
            </div>            
        </div>
        </div>
</>
    );
    
}
export default QuizPage;